import React, { useState } from 'react';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Divider from '@mui/material/Divider';
import Chip from '@mui/material/Chip';

export default function ExercicioTres(){
    const [numero, setNumero] = useState('');
    const [resultado, setResultado] = useState('');

    const handleClick = () => {
        if(Number(numero) % 2 === 0){
            setResultado(`O numero  par`)
        } else {
            setResultado(`O numero  impar`)
        }
    }

    return(
        <div className ="app">
            <Box id="ex03" mb={3}>
                <h1>Exercicio 03:</h1>
                <span>
                    Faa um programa que receba um nmero inteiro e verifique se este nmero  par ou mpar.
                    Identificamos se um nmero  par ou mpar quando o dividimos por dois. Se o resto da diviso
                    for zero, o nmero  par, caso contrrio,  mpar.
                </span>
            </Box>
            <Box sx={{'& > :not(style)': { m: 1, width: '25ch' }}}>
                <TextField label="Digite um numero" size="small" value={numero} onChange={e => setNumero(e.target.value)}/>
            </Box>
            <div>
                <Button variant="text" onClick={handleClick}>Testar</Button>
                <p>{resultado}</p>
            </div>
            <Divider textAlign='right'><Chip label="Next" /></Divider>
        </div>
    )
}